@use "../media-queries.scss" as *;

%flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

%trans {
  transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  -webkit-transition: 0.5s ease-in-out;
}

%absolute-full {
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.navbar {
  @extend %trans;
  font-size: 1.5rem;
  color: var(--p-color);
  padding: 1rem;
  width: 100%;
  height: 100px;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;

  @include mq(medium) {
    height: 120px;
    padding: 1rem 15% 1rem 15%;
  }
}

.navbar #image-wrapper-white {
  @extend %flex-center;
  height: 100%;
  padding: 0 1rem;
  opacity: 1;

  & img {
    position: relative;
    width: auto;
    height: 60%;
    margin: 0 var(--large);
  }
}

.navbar #image-wrapper-blue {
  @extend %flex-center, %trans;
  height: 100%;
  padding: 0 1rem;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;

  & img {
    position: relative;
    width: auto;
    height: 60%;
    margin: 0 var(--large);
  }

  @include mq(medium) {
    left: 15%;
  }
}

.navbar__container {
  @extend %flex-center;
  height: 100%;
  justify-content: space-between;
}

.navbar__logo {
  background-color: transparent;
  padding: 0;
  height: 90%;
}

.navbar__logo img {
  height: 100%;
  width: 100%;
}

.current {
  transition: ease-in-out 0.3s;
}

.active {
  display: flex !important;
}

button,
a {
  cursor: pointer;
}

.nav-items,
.nav-items-instant {
  display: flex;
  width: 100%;
  margin: auto;
  justify-content: space-evenly;
  padding: 0;
  align-items: center;
  flex-direction: column;
  list-style-type: none;
  color: var(--clr-contrast-100);
  font-size: clamp(2rem, 7vw, 50px);
  font-weight: inherit;
}

:is(.nav-items-instant, .nav-items) .nav-item {
  position: relative;
  padding-top: clamp(0.3rem, 3vw, 1rem);
  text-align: center;
  margin: 0.5rem 0;
}

.nav-items .nav-item:last-of-type {
  margin-bottom: clamp(0.3rem, 3vw, 1rem);
}

:is(.nav-items-instant, .nav-items) .nav-item a {
  @extend %trans;
  display: flex;
  color: var(--b-text);
  text-decoration: none;
  width: 100%;
  height: 100%;
  font-weight: inherit;
  font-size: var(--fs-sub-big);
  text-align: center;
}

.nav-items .nav-item a::before {
  content: attr(data-attribute-text);
}

.nav-items .nav-item a:hover {
  color: var(--clr-secondary-100);
  text-decoration: none;
}

.nav-items-mobile {
  @extend %flex-center, %absolute-full;
  position: fixed;
  margin: 0;
  padding: 0;
  flex-direction: column;
  list-style-type: none;
  z-index: -1;
  background-color: var(--clr-contrast-800);

  @include mq(medium) {
    display: none;
  }
}

.visible {
  z-index: 10;
}

/* desktop navigation*/
.nav-items-desktop {
  display: none;
  position: fixed;
  justify-content: center;
  padding: 0;
  margin: 0 0 0 auto;
  right: 0;
  height: 100%;
  align-items: center;
  flex-direction: column;
  list-style-type: none;
  left: 0;
  top: 0;

  @include mq(medium) {
    position: relative;
    justify-content: space-evenly;
    padding: 0;
    margin: 0 0 0 auto;
    right: 0;
    height: 100%;
    align-items: center;
    flex-direction: row;
    list-style-type: none;
    display: flex;
  }
}

:is(.nav-items-desktop, .nav-items-mobile) li {
  display: flex;
  align-items: center;
  margin: 1rem auto;
  // &:first-of-type a{
  //   padding-left: 0
  // }
  // &:last-of-type a{
  //   padding-right: 0;
  // }
}

:is(.nav-items-desktop, .nav-items-mobile) li .nav-link {
  @extend %trans;
  background-color: transparent;
  position: relative;
  display: flex;
  align-items: center;
  padding: 0;
  height: 100%;
  color: var(--clr-contrast-100);
  text-decoration: none;
  font-size: clamp(1.5rem, 15vw, 2rem);
  font-weight: 800;

  border-radius: 3px;
  font-family: "Fira Sans", sans-serif;
  animation: empty 1s infinite;

  &:hover,
  &:active {
    animation: line 5s infinite;
    cursor: pointer;
  }

  &:hover::after,
  &:active::after {
    animation: glitchMain 6s infinite;
    color: var(--clr-primary-200);
    animation-timing-function: step-start;
    opacity: 0.5;
    transform: translate(-20px, -10px);
  }

  &:hover::before,
  &:active::before {
    animation: glitchMain 9s infinite;
    color: var(--clr-primary-400);
    animation-timing-function: step-start;
    opacity: 0.5;
    transform: translate(8px, 10px);
  }

  @include mq(medium) {
    font-size: clamp(1rem, 5vw, 1.5rem);
    padding: 1rem;
  }

  @include mq(large) {
    padding: 1.5rem;
  }
}

@keyframes line {
  0% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      0 100%,
      0 100%,
      100% 100%,
      100% 100%,
      0 100%,
      0 100%,
      100% 100%,
      100% 0%
    );
  }

  1% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      0 100%,
      0 92%,
      100% 92%,
      100% 97%,
      0 97%,
      0 100%,
      100% 100%,
      100% 0%
    );
  }

  99% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      0 100%,
      0 5%,
      100% 5%,
      100% 11%,
      0 11%,
      0 100%,
      100% 100%,
      100% 0%
    );
  }

  100% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      0 100%,
      0 5%,
      100% 5%,
      100% 0,
      0 0,
      0 100%,
      100% 100%,
      100% 0%
    );
  }
}

@keyframes glitchMain {
  0% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      25% 100%,
      25% 25%,
      75% 25%,
      75% 75%,
      25% 75%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  9% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      23% 100%,
      23% 33%,
      100% 33%,
      100% 100%,
      25% 100%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  10% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      8% 100%,
      7% 13%,
      39% 13%,
      40% 76%,
      7% 76%,
      8% 100%,
      100% 100%,
      100% 0%
    );
  }

  12% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      25% 100%,
      25% 25%,
      75% 25%,
      75% 75%,
      25% 75%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  20% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      23% 100%,
      23% 33%,
      100% 33%,
      100% 100%,
      25% 100%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  50% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      25% 100%,
      25% 25%,
      75% 25%,
      75% 75%,
      25% 75%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  55% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      23% 100%,
      23% 33%,
      100% 33%,
      100% 100%,
      25% 100%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  58% {
    clip-path: inset(5% 20% 15% 10%);
  }

  60% {
    clip-path: inset(5% 20% 15% 10%);
  }

  61% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      8% 100%,
      7% 13%,
      39% 13%,
      40% 76%,
      7% 76%,
      8% 100%,
      100% 100%,
      100% 0%
    );
  }

  70% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      25% 100%,
      26% 0,
      100% 0,
      100% 75%,
      25% 75%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  75% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      25% 100%,
      25% 25%,
      75% 25%,
      75% 75%,
      25% 75%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  77% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      8% 100%,
      7% 13%,
      39% 13%,
      40% 76%,
      7% 76%,
      8% 100%,
      100% 100%,
      100% 0%
    );
  }

  80% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      23% 100%,
      23% 33%,
      100% 33%,
      100% 100%,
      25% 100%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  85% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      25% 100%,
      25% 25%,
      75% 25%,
      75% 75%,
      25% 75%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  90% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      0 100%,
      0 25%,
      100% 25%,
      100% 75%,
      0 75%,
      0 100%,
      100% 100%,
      100% 0%
    );
  }

  95% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      25% 100%,
      25% 0,
      74% 0,
      75% 100%,
      26% 100%,
      25% 100%,
      100% 100%,
      100% 0%
    );
  }

  100% {
    clip-path: polygon(
      0% 0%,
      0% 100%,
      8% 100%,
      7% 13%,
      39% 13%,
      40% 76%,
      7% 76%,
      8% 100%,
      100% 100%,
      100% 0%
    );
  }
}

.white li a {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);

  &:hover {
    color: var(--clr-primary-200);
    background-color: rgba(14, 22, 36, 0.1);
  }
}

.nav-items-desktop li .nav-link::after,
.nav-items-desktop li .nav-link::before {
  transition: ease-in-out 0.5s;
  content: attr(data-link-text);
  animation: empty 1s infinite;
  font-weight: 800;
  font-size: var(--fs-sub-small);
  display: flex;
  opacity: 0;
  z-index: -1;
  align-items: center;
  position: absolute;
}

@keyframes empty {
}

.shrunk {
  background-color: var(--clr-contrast-800);
}

.instant-nav {
  position: absolute;
  background: transparent;
  opacity: 1;
  right: 5%;
  bottom: 5%;
  transition: ease-in-out 0.2s;

  @include mq(medium) {
    display: none;
  }
}

.instant-nav .fas {
  padding: var(--medium);
  color: var(--b-text);
  background: var(--cta);
}

.instant-nav {
  width: 100%;
  bottom: 0;
  position: fixed;
  left: 0;
  z-index: 12;
}

.instant-nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  list-style-type: none;
  margin: 0;
  padding: 0 0 1rem 0;
}

.instant-nav ul li {
  width: auto;
  text-align: center;
  padding: 1rem 3rem;
  color: var(--clr-contrast-100);
  text-shadow: 2px 2px 4px rgb(0 0 0 / 20%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  box-shadow: 2px 2px 4px rgb(0 0 0 / 20%);
}

.instant-nav ul li i {
  font-size: 25px;
  font-weight: inherit;
}

.instant-nav ul li span {
  text-transform: uppercase;
  font-weight: inherit;
  margin-top: 0.75rem;
  font-size: 10px;
}

//additions to standard css

.instant-nav ul li {
  background-image: none;
  background-color: var(--clr-contrast-100);
  color: var(--clr-primary-300);
}

.instant-nav ul li:first-child {
  background-image: -webkit-linear-gradient(
    35deg,
    var(--clr-secondary-200),
    var(--clr-secondary-100)
  );
  color: var(--clr-contrast-100);
}
